import fetch from '../config/fetch'

// 
if (process.env.NODE_ENV === 'development') {
    /**
     *  
     * @param {*} name
     */
    var getNewestVideos = pageNum => fetch('GET', '/GetNewstVideos', { p: pageNum }, 'axios', true)
} else {

}

export { getNewestVideos }


